<template>
  <div id="app">
      <PreGame v-if="!gameReady" @gameStart='startGame' />
      <Game v-if="gameReady" :players='players' :limit='limit' />
  </div>  
</template>

<script>
import PreGame from './components/PreGame'
import Game from './components/Game'
export default {
  name: "App",
  data() {
    return {
      gameReady: false
    }
  },
  components: {
    PreGame,
    Game
  },
 methods: {
   startGame(players, limit) {
     this.gameReady = true
     this.players = players
     this.limit = limit
   }
 },
};
</script>

