<template>
  <div>
    <b-container>
      <form>
        <b-row>
          <b-col cols="12" class="mt-2 mb-2" v-for="(player, index) in players" :key="index">
            <div class="form-gruop">
              <input
                type="text"
                class="form-control"
                :placeholder="allPlayers[index].playerName"
                v-model.number="allPlayers[index].roundScore"
                required
                :disabled="gameIsOver"
              />
            </div>
          </b-col>
        </b-row>
      </form>
      <button
        class="btn btn-primary mt-3 mb-3"
        @click.prevent="endRound"
        v-if="!gameIsOver"
      >Kraj Runde</button>
      <button class="btn btn-danger mt-3 mb-3" @click.prevent="restartGame" v-else>Restart</button>
      <hr />
      <b-row align-h="around">
        <b-col cols="6" md="3" >
          <p class="bg-secondary  rounded border border-dark shadow p-3 mb-3 bg-white rounded">
            Meša:
            <span class="h5">
              <b>{{ dealer }}</b>
            </span>
          </p>
        </b-col>
        <b-col cols="6" md="3">
          <p  class="bg-secondary  rounded border border-dark shadow p-3 mb-3 bg-white rounded">
            <b>Igra se do: {{ limit }}p</b>
          </p>
        </b-col>
      </b-row>

      <b-row class="mb-0 mt-2">
        <b-col v-for="(players, index) in allPlayers" :key="index">
          <h4>
            {{ players.playerName }}:
            <br />
            {{players.totalScore}}
          </h4>
        </b-col>
      </b-row>
      <hr class="mb-3" />
      <b-table-simple responsive striped hover >
        <b-thead>
          <b-tr>
            <b-th sticky-column v-for="(player, index) in players" :key="index">{{ player }}</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr v-for="(round, index) in rounds" :key="index">
            <b-th sticky-column v-for="(value,index) in round" :key="index">{{ value }}</b-th>
          </b-tr>
        </b-tbody>
      </b-table-simple>
    </b-container>
  </div>
</template>

<script>
export default {
  props: ["players", "limit"],
  data() {
    return {
      allPlayers: [],
      rounds: [],
      numberOfRounds: 0,
      dealer: "",
      gameIsOver: false
    };
  },
  methods: {
    endRound() {
      //na kraju svake runde moramo da:
      //uzmemo roundScore za svakog playera i ubacujemo taj objekat u rounds niz
      //1

      let score = this.allPlayers.map(a => a.roundScore);
      let res = score.every(function(element) {
        return typeof element === "number";
      });
      if (res) {
        this.rounds.push(score);
        this.numberOfRounds++;

        // ko mesa?
        let numberOfPlayers = this.players.length;
        let playerDealing = this.numberOfRounds % numberOfPlayers;

        this.dealer = this.players[playerDealing];

        //sabiramo totalScore za svakog playera pojedinacno tako sto uzimamo prethodni total score i sabiramo ga sa roundScore trenutne runde.... za svakog plejera... pojedinacno... O.O
        this.calculateTotalScore();
        this.gameOver();
        this.resetFields();
      } else {
        alert("Sva polja moraju biti popunjena");
      }
    },

    calculateTotalScore() {
      for (let i = 0; i < this.allPlayers.length; i++) {
        this.allPlayers[i].totalScore += this.allPlayers[i].roundScore;
      }
    },
    resetFields() {
      for (let i = 0; i < this.allPlayers.length; i++) {
        this.allPlayers[i].roundScore = null;
      }
    },
    gameOver() {
      let limit = this.limit;

      let score = this.allPlayers.map(a => a.totalScore);
      let result = score.some(function(element) {
        return element >= limit;
      });
      if (result) {
        alert("Kraj");
        this.gameIsOver = true;
      }
    },
    restartGame() {
      if (confirm("Počni ispočetka?")) {
        this.startTheGame();
        (this.gameIsOver = false),
          (this.rounds = []),
          (this.numberOfRounds = 0);
      }
    },
    startTheGame() {
      for (let index = 0; index < this.players.length; index++) {
        this.allPlayers[index] = {
          playerName: this.players[index],
          roundScore: null,
          totalScore: 0
        };
      }
      this.dealer = this.players[0];
    }
  },

  created() {
    this.startTheGame();
  }
};
</script>

