<template>
  <div>
    <b-container>
      <img
        alt="Uno logo"
        width="33%"
        src="https://uproxx.com/wp-content/uploads/2017/09/uno-color-blind-colorblindness-deck_mattel-0.gif"
      />
      <b-row class="mt-3 mb-3">
        <b-col cols="12" sm="4" offset-sm="4">
          <b-form>
            <b-input-group>
              <b-form-input placeholder="Ime" v-model="player" @keyup.prevent.enter="addPlayer"></b-form-input>
              <b-input-group-append>
                <button class="btn btn-primary" @click.prevent="addPlayer">Dodaj igrača</button>
              </b-input-group-append>
            </b-input-group>
          </b-form>
        </b-col>
      </b-row>

      <div class="mb-3">
        <h5>Igrači:</h5>
        <b-row fluid>
          <b-col v-for="(player, index) in players" :key="index">
            <p>{{ index+1 }}) {{ player }}</p>
          </b-col>
        </b-row>

        <b-row class="mt-3 mb-3">
          <b-col cols="12" sm="2" offset-sm="5">
            <label>Dokle Igramo?</label>
            <b-form-input type="text" id="limit" placeholder="Dokle igramo?" v-model.number="limit"></b-form-input>
          </b-col>
        </b-row>
        <a @click.prevent="startGame" v-if="players.length > 0">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          Start
        </a>
      </div>
    </b-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      players: [],
      player: "",
      limit: 501
    };  
  },
  methods: {
    addPlayer() {
      if (this.player.trim() !== "") {
        this.players.push(this.player);
        this.player = "";
      }
    },
    startGame() {
      
      this.$emit("gameStart", this.players, this.limit);
    }
  }
};
</script>

<style  scoped>
a {
  position: relative;
  display: inline-block;
  padding: 25px 30px;
  margin: 0 40px;
  color: #03e9f4;
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  cursor: pointer;
  font-weight: 700;
  transition: 0.5s;
  letter-spacing: 8px;
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
  filter: hue-rotate(290deg);
}

a:hover {
  background: #03e9f4;
  color: #050801;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
    0 0 200px #03e9f4;
}

a span {
  position: absolute;
  display: block;
}

a span:nth-child(1) {
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #03e9f4);
  animation: animate1 1s linear infinite;
}

@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

a span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #03e9f4);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

a span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #03e9f4);
  animation: animate3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes animate3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

a span:nth-child(4) {
  left: 0;
  bottom: -100%;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #03e9f4);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
</style>